@import url(https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,500;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  display: flex;
}
.nav-cont {
  width: 100px;
  display: flex;
  flex-flow: column;
  position: relative;
  min-height: 100vh;
  background: radial-gradient(circle, #70396cd7 0%, #680b1399 100%); }
  @media screen and (max-width: 480px) {
    .nav-cont {
      display: none; } }

.nav-left-cont {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-flow: column;
  text-align: left;
  margin-top: 1rem; }
  .nav-left-cont a {
    color: #ffffff;
    text-decoration: none; }

.logo-cont {
  display: flex;
  flex-flow: column;
  cursor: pointer; }
  .logo-cont a {
    margin-top: 1rem;
    color: inherit;
    text-decoration: none;
    display: flex;
    align-items: center;
    flex-flow: column; }
  .logo-cont h1 {
    display: none;
    color: white;
    font-weight: 500;
    font-size: 1.5rem;
    margin-left: .5rem; }
  .logo-cont img {
    display: block;
    max-width: 28px; }

.cart-cont {
  display: flex;
  margin-top: 1rem;
  justify-content: center;
  align-items: center;
  cursor: pointer; }
  .cart-cont img {
    width: 25px;
    fill: white; }

.nav-items-list {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin: .5rem;
  flex-flow: column;
  padding: 0; }
  .nav-items-list a {
    padding: .5rem; }

body {
  background-color: #f0f2f5; }

.home-cont {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 3rem; }

.product-item-cont {
  display: flex;
  flex-flow: column;
  align-items: center;
  margin: .5rem;
  padding: .5rem;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3);
  transition: box-shadow 150ms; }
  .product-item-cont a {
    text-decoration: none;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    height: 100%; }
  .product-item-cont:hover {
    box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.3); }
  .product-item-cont .product-img img {
    max-width: 280px;
    width: 100%; }

.product-decription h2 {
  font-size: 1.2rem;
  font-family: 'Ubuntu', sans-serif;
  font-weight: 300;
  color: #272727;
  text-align: center; }

.product-list {
  width: 100%;
  max-width: 1440px;
  display: grid;
  padding: 1rem;
  margin-bottom: 2rem;
  margin-left: 2rem;
  margin-right: 2rem;
  background-color: white;
  box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.3);
  grid-template-columns: 1fr 1fr 1fr 1fr; }
  @media screen and (max-width: 1280px) {
    .product-list {
      grid-template-columns: 1fr 1fr 1fr; } }
  @media screen and (max-width: 780px) {
    .product-list {
      grid-template-columns: 1fr 1fr; } }
  @media screen and (max-width: 480px) {
    .product-list {
      grid-template-columns: 1fr;
      box-shadow: none; } }

.product-list-cont {
  display: flex;
  flex-flow: column;
  align-items: center; }

.pagination-cont {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1440px;
  background-color: white;
  margin-bottom: 2rem;
  box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.3); }
  .pagination-cont .pagination {
    width: 100%;
    max-width: 480px;
    display: flex;
    justify-content: space-between; }
    .pagination-cont .pagination li {
      list-style-type: none; }

.auth-cont {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }

.log-in {
  display: flex;
  width: 100%;
  max-width: 400px;
  flex-flow: column;
  align-items: center;
  box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.3);
  border-radius: 7px;
  background-color: white;
  padding: 1rem; }
  .log-in .p-inputtext {
    margin-top: .5rem;
    margin-bottom: .5rem; }

.dashbord-main-cont {
  width: 100%;
  display: grid;
  position: relative;
  background: radial-gradient(circle, #b7bee2 0%, #ace4de 100%);
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif; }

.side-btn {
  position: fixed !important;
  left: 7%;
  top: 5%;
  z-index: 100; }

.sidebar-cont {
  position: relative;
  z-index: 11111; }
  .sidebar-cont li {
    list-style-type: none;
    cursor: pointer;
    border-radius: 5px;
    padding: 1em;
    margin: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: background-color 0.3s ease; }
    .sidebar-cont li a {
      display: block;
      width: 100%;
      height: 100%;
      color: #000000e1; }
    .sidebar-cont li:hover {
      background-color: #e0e0e0; }

.main-cont {
  position: relative;
  grid-column: 2 / span 7;
  grid-row: 1;
  margin: 2rem; }

.products-table {
  margin-top: 2rem;
  margin-bottom: 2rem; }

.list-cont {
  position: relative;
  grid-column: 2 / span 7;
  grid-row: 1;
  margin: 2rem; }

.add-form-cont {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  padding: 1rem;
  background-color: white;
  display: flex;
  justify-content: center; }
