.product-item-cont {
    display: flex;
    flex-flow: column;
    align-items: center;
    margin: .5rem;
    padding: .5rem;
    //border-radius: 2px
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.3);
    transition: box-shadow 150ms;
    a {
        text-decoration: none;
        display: flex;
        flex-flow: column;
        justify-content: space-between;
        height: 100%; }
    &:hover {
        box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.3);
 }        //transform: scale(1.05)
    .product-img {
        img {
            max-width: 280px;
            width: 100%; } } }



.product-decription {
    h2 {
        font-size: 1.2rem;
        font-family: 'Ubuntu', sans-serif;
        font-weight: 300;
        color: #272727;
        text-align: center; } }
